		onCollection(){
			this.isCollection = !this.isCollection;
		},
		// 点击客服icon
		onCustomer(){
			
		},
		// 发起拼单
		onStartFight(){
			this.isShowPopup = true;
		},
		// 单独购买
		onAloneShop(){
			this.isShowPopup = true;
		},
		// 点击关闭
		onClose(){
			this.isShowPopup = false;
		},
		// 点击样式选择，尺码，颜色
